	module.exports = {"data":{"post":{"html":"<p>After a high fever over the weekend, we thought Christopher was over it on Monday. But he isn't. He's got an ear infection, so it's back on antibiotics, and no play groups for a week. But he doesn't look or act sick, and his appetite is good.</p>\n<p>Tomorrow is just another work day for me, but Christopher will be celebrating with a toddler-sized jar of <a href=\"http://www.hipp.de\">Hipp</a> turkey and vegetables!</p>\n<p>In our excitement of staying-home-while-Mom's-on-a-business-trip, we forgot to put out the trash on Monday night. That's bad. Our only trashbin is plump full of diapers, and the next pick-up is in two weeks.</p>","fields":{"title":"Not again","date":"22 Nov 2000","url":"https://papascott.de/2000-11-22-not-again/"}}},"pathContext":{"slug":"/2000-11-22-not-again/","prev":{"fields":{"title":"Short and snappy","date":"21 Nov 2000","slug":"/2000-11-21-short-and-snappy/"}},"next":{"fields":{"title":"Never leave your child unattended....","date":"24 Nov 2000","slug":"/2000-11-24-never-leave-your-child-unattended/"}}}}

